header {
	width: 24.4%;
	height: 108px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	background: $ikeaBl;
	position: fixed;
	top: 0;
	right: 0;
	z-index: 999;

	// header Box
	.menu {
		flex: 1.5;
		font: 35px/110px $ikeaFontKo;
		color: $grey;
		cursor: pointer;

		//header open button
		.fa-bars-staggered {
			margin-left: 50px;
			rotate: 90deg;
		}

		//header close button
		.fa-xmark {
			margin-left: 50px;
			animation: roateCloseBtn 0.5s ease-in;
			animation-fill-mode: backwards;
			animation-duration: 0.5s;
			display: none;

			@keyframes roateCloseBtn {
				from {
					transform: rotate(0deg);
				}
				to {
					transform: rotate(720deg);
				}
			}
		}
	}

	//header title
	.logo {
		flex: 4;
		cursor: pointer;

		h1 {
			font: bold 35px/110px $ikeaFontKo;
			color: $grey;
			margin-right: 50px;

			a {
				cursor: pointer;

				&.on {
					color: aqua;
				}
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	header {
		width: 37%;

		.menu {
			font: 35px/108px $ikeaFontKo;
		}

		.logo {
			margin-left: 20px;
		}
	}
}

@media screen and (max-width: $mobile) {
	header {
		width: 50%;
		height: 95px;

		.menu {
			font: 30px/95px $ikeaFontKo;

			.fa-bars-staggered {
				margin-left: 50px;
			}
		}

		.logo {
			h1 {
				font: bold 30px/95px $ikeaFontKo;
				margin-left: 20px;
			}
		}
	}
}

